.container {
  top: 0px;
  left: 0;
  position: fixed;
  width: 100%;
  background-color: var(--transparent-navy);
  box-shadow: var(--navy-shadow);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 110;
  padding-top: 15px;
}

.left {
  padding-left: 50px;
  display: flex;
  align-items: center;
}
.left > a {
  color: var(--green);
  font-size: var(--fz-xxl);
  font-weight: bold;
  font-family: var(--font-sans);
}

.right {
  padding-left: 50px;
  display: flex;
  align-items: center;
}
.link {
  /* border: 1px solid red; */
  font-size: var(--fz-sm);
  color: ar(--light-slate);
  cursor: pointer;
  margin-left: 25px;
}

.link:hover {
  color: var(--green);
}
.resumeBtn {
  outline: none;
  font-size: var(--fz-xs);
  background: transparent;
  border: 1px solid var(--green);
  color: var(--green);
  border-radius: 5px;
  padding: 10px 12px;
  cursor: pointer;
  margin: 0 25px;
}
.resumeBtn:hover {
  background: var(--green-tint);
}

.right1 {
  padding: 0px 50px;
  display: none;
  align-items: center;
}
@media screen and (max-width: 600px) {
  .right {
    display: none;
  }
  .right1 {
    display: flex;
  }
}
@media screen and (min-width: 601px) {
  .right {
    display: flex;
  }
  .right1 {
    display: none;
  }
}
@media screen and (min-width: 320px) and (max-width: 786px) {
  .left {
    padding-left: 20px;
  }
  .left > a {
    font-size: 1.7rem;
  }
}
@media screen and (min-width: 787px) and (max-width: 1200px) {
  .left > a {
    font-size: 2rem;
  }
  .right > a {
    font-size: var(--md);
  }
}
@media screen and (min-width: 1200px) and (max-width: 2700px) {
  .left > a {
    font-size: 2rem;
  }
  .right > a {
    font-size: var(--lg);
  }
  .resumeBtn {
    font-size: var(--fz-sm);
  }
}
