.container {
  width: calc(100%-50px);
  height: 100vh;
  max-height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: var(--font-sans);
}

.greet {
  font-family: var(--font-mono);
  font-size: var(--fz-xl);
  color: var(--green);
  margin: 10px 0;
}

.heading_1 {
  color: var(--lightest-slate);
  font-size: 70px;
  margin: 0;
}

.name {
  font-size: 50px;
  margin: 0;
  color: var(--slate);
}

.btn {
  outline: none;
  font-size: var(--fz-md);
  background: transparent;
  border: 1px solid var(--green);
  color: var(--green);
  border-radius: 5px;
  padding: 15px 20px;
  width: 200px;
  cursor: pointer;
  margin-top: 15px;
}

.btn:hover {
  background: var(--green-tint);
}
@media screen and (max-width: 500px) {
  .container {
    height: 105vh;
  }
  .heading_1 {
    font-size: 40px;
  }

  .name {
    font-size: 20px;
  }
}

@media screen and (min-width: 501px) and (max-width: 786px) {
  .heading_1 {
    font-size: 50px;
  }

  .name {
    font-size: 30px;
  }
}
@media screen and (min-width: 787px) and (max-width: 1200px) {
  .heading_1 {
    font-size: 60px;
  }

  .name {
    font-size: 38px;
  }
}
@media screen and (min-width: 1200px) {
  .heading_1 {
    font-size: 70px;
  }

  .name {
    font-size: 50px;
  }
}
