.sideBar {
  position: fixed;
  width: 200px;
  background-color: var(--navy);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  padding-top: 50px;
  padding-bottom: 20px;
  z-index: 200;
  border-radius: 7rem 0 0 2.5rem;
  outline: none !important;
  border-color: var(--green);
  box-shadow: 0 0 7px var(--green);
}
.close {
  /* border: 2px solid red; */
  display: flex;
  position: fixed;
  top: 15px;
  right: 15px;
}

.linkDiv {
  /* border: 1px solid goldenrod; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 20px;
  border-radius: 2rem;

  /* // */

  transition: 0.3s ease;
  /* display: none;
  display: block; */
}

.link {
  /* border: 1px solid red; */
  font-size: var(--fz-xl);
  color: ar(--light-slate);
  cursor: pointer;
  margin: 20px;
}

.link:hover,
.link:active {
  color: var(--green);
}

.resumeBtn {
  outline: none;
  font-size: var(--fz-md);
  background: transparent;
  border: 1px solid var(--green);
  color: var(--green);
  border-radius: 5px;
  padding: 10px 12px;
  cursor: pointer;
  margin: 0 25px;
  margin-top: 10px;
}
.resumeBtn:hover {
  background: var(--green-tint);
}
