.container {
  position: relative;
  width: calc(100% - 300px);
  /* width: calc(100%); */
  top: 0;
  left: 150px;
  z-index: 5;
  box-sizing: border-box;
}
@media screen and (max-width: 500px) {
  .container {
    width: calc(100% - 10px);
    /* width: calc(100%); */
    top: 0;
    left: 10px;
    right: 10px;
    padding: 0 10px;
  }
}
@media screen and (min-width: 501px) and (max-width: 999px) {
  .container {
    width: calc(100% - 30px);
    /* width: calc(100%); */
    top: 0;
    left: 20px;
    right: 30px;
    padding: 0 30px;
  }
}
