.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.project {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.25) 0px -12px 30px, #a5f1e9 0px 4px 6px,
    rgba(0, 0, 0, 0.25) 0px 12px 13px, #a5f1e9 0px -3px 5px;
  padding: 18px;
  border-radius: 1rem;
}

.left {
  flex: 1;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.img {
  width: 600px;
  height: 300px;
  object-fit: cover;
  border-radius: 5px;
  transition: transform 0.5s ease;
}
.img:hover {
  transform: scale(1.05);
}

.right {
  flex: 1;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}

.heading {
  color: var(--lightest-slate);
  font-family: var(--font-sans);
  font-size: var(--fz-heading);
  font-weight: 600;
  cursor: pointer;
}

.heading:hover {
  color: var(--green);
}

.desc {
  background-color: var(--light-navy);
  box-shadow: 0 10px 30px -15px var(--navy-shadow);
  color: var(--light-slate);
  font-family: var(--font-sans);
  line-height: 20px;
  font-size: var(--fz-md);
  padding: 20px;
  z-index: 2;
  width: 120%;
  border-radius: 5px;
}

.skill_container {
  font-size: var(--fz-sm);
  margin-bottom: 12px;
  display: flex;
}

.skill_container > p {
  margin: 0;
  margin-left: 12px;
  transition: transform 0.5s ease;
}
.skill_container > p:hover {
  color: var(--green);
  transform: scale(1.2);
}

.links_container {
  display: flex;
}

.link {
  cursor: pointer;
  margin-left: 20px;
}

.link:hover {
  color: var(--green);
}
@media screen and (max-width: 600px) {
  .project {
    flex-direction: column;
    padding: 10px;
  }
  .left {
    width: 100%;
    text-align: center;
  }
  .img {
    width: 90%;
  }
  .right {
    width: 95%;
    text-align: center;
    box-sizing: border-box;
  }
  .heading {
    font-size: var(--fz-heading);
    margin-top: 10px;
  }
  .desc {
    font-size: var(--fz-md);
    padding: 2px;
    width: 95%;
    border-radius: 5px;
  }

  .skill_container {
    font-size: var(--fz-sm);
  }

  .links_container {
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 601px) and (max-width: 1400px) {
  .project {
    flex-direction: row;
    padding: 10px;
  }
  .left {
    width: 100%;
    text-align: center;
  }
  .img {
    width: 90%;
  }
  .right {
    width: 95%;
    text-align: center;
    box-sizing: border-box;
  }
  .heading {
    font-size: var(--fz-heading);
    margin-top: 10px;
  }
  .desc {
    font-size: var(--fz-md);
    padding: 2px;
    width: 110%;
    border-radius: 5px;
  }

  .skill_container {
    font-size: var(--fz-sm);
  }

  .links_container {
    margin-bottom: 10px;
  }
}
