.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 200px);
}

.left {
  flex: 1;
}

.desc {
  font-family: var(--font-sans);
  color: var(--slate);
  font-size: var(--fz-lg);
}
.desc::first-letter {
  font-size: 2rem;
  font-weight: bold;
  color: var(--slate);
}

.right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile_img {
  width: 300px;
  border-radius: 10px;
}

.img_border {
  width: 300px;
  height: 300px;
  position: absolute;
  border: 2px solid var(--green);
  border-radius: 10px;
  margin-top: 30px;
  margin-left: 30px;
  transition: var(--transition);
  z-index: -1;
  border: 1px solid yellowgreen;
}

.profile_img:hover ~ .img_border {
  margin-top: 15px;
  margin-left: 15px;
}
@media screen and (max-width: 599px) {
  .container {
    height: auto;
    flex-direction: column-reverse;
    gap: 25px;
    padding: 20px 0;
  }
  .profile_img {
    width: 230px;
    border-radius: 8px;
    margin-top: 10px;
  }

  .img_border {
    width: 230px;
    height: 230px;
  }
  .profile_img:hover ~ .img_border {
    margin-top: 16px;
    margin-left: 16px;
  }
}

@media screen and (min-width: 601px) and (max-width: 786px) {
  .container {
    height: auto;
    /* height: calc(50vh - 200px); */
    padding: 40px 0;
  }
  .desc {
    font-size: var(--fz-sm);
  }
  .profile_img {
    width: 200px;
    border-radius: 8px;
  }

  .img_border {
    width: 200px;
    height: 200px;
  }
  .profile_img:hover ~ .img_border {
    margin-top: 15px;
    margin-left: 15px;
  }
  .desc::first-letter {
    font-size: 1.5rem;
  }
}
@media screen and (min-width: 787px) and (max-width: 1200px) {
