  }

  .img_border {
    width: 200px;
    height: 200px;
  }
  .profile_img:hover ~ .img_border {
    margin-top: 18px;
    margin-left: 18px;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1800px) {
  .container {
    height: auto;
    /* height: calc(90vh - 200px); */
  }
  .desc {
    font-size: var(--fz-md);
  }
