.project {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
  width: 100%;
  height: 100vh;
  /* max-height: 800px; */
  width: 100%;
  font-family: var(--font-sans);
  position: relative;
  margin-bottom: 5rem;
}

.heading_2 {
  font-size: 50px;
  margin: 0;
}

.desc {
  font-size: var(--fz-lg);
  width: 70%;
  text-align: left;
  color: var(--slate);
}

.btn {
  outline: none;
  font-size: var(--fz-md);
  background: transparent;
  border: 1px solid var(--green);
  color: var(--green);
  border-radius: 5px;
  padding: 15px 20px;
  width: 150px;
  cursor: pointer;
  margin-top: 20px;
  align-self: flex-start;
  margin-left: 0;
}

.btn:hover {
  background: var(--green-tint);
}

.left {
  box-sizing: border-box;
  flex: 1;
  width: 40%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.right {
  box-sizing: border-box;
  flex: 1;
  width: 60%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.form {
  /* background-color: var(--light-navy); */
  box-sizing: border-box;
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
}
.form input,
.form textarea {
  box-sizing: border-box;
  width: 100%;
  padding: 20px 12px;
  border: none;
  border-bottom: 1px solid var(--lightest-slate);
  border-radius: 0.5rem;
  margin: 10px 0;
  font-size: var(--fz-xxl);
  font-weight: 300;
  background-color: transparent;
  color: var(--white) !important;
  word-spacing: 5px;
  letter-spacing: 3px;
  font-family: var(--font-sans);
}
.form textarea {
  overflow: hidden;
}
.form input::placeholder,
.form textarea::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--slate);
  opacity: 1; /* Firefox */
}

.form input:-ms-input-placeholder,
.form textarea:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: var(--slate);
}

.form input::-ms-input-placeholder,
.form textarea::-ms-input-placeholder {
  /* Microsoft Edge */
  color: var(--slate);
}

input:focus,
textarea:focus {
  outline: none !important;
  border-color: var(--green);
  box-shadow: 0 0 10px var(--green);
}
.contacts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.contact:hover {
  color: var(--green);
}
.contact > span {
  margin-left: 15px;
}
.block {
  display: block;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 1000px) {
  .block {
    display: none;
  }
}
@media screen and (max-width: 799px) {
  .project {
    flex-direction: column;
    height: auto;
  }
  .left {
    width: 100%;
    align-items: left;
    padding-bottom: 50px;
  }
  .heading_2 {
    font-size: 40px;
  }

  .desc {
    width: 99%;
    text-align: left;
    color: var(--slate);
  }
  .right {
    width: 100%;
    align-items: center;
  }
  .form {
    width: 90%;
  }
  .form input,
  .form textarea {
    padding: 10px 12px;
    margin: 10px 0;
    font-size: var(--fz-l);
    font-weight: 300;
    word-spacing: 2px;
    letter-spacing: 2px;
  }
  .btn {
    font-size: var(--fz-md);
    padding: 15px 20px;
    width: 100%;
    margin-top: 20px;
    align-self: center;
  }
}

@media screen and (min-width: 800px) and (max-width: 1500px) {
  .project {
    flex-direction: row;
    height: auto;
  }
  .left {
    width: 95%;
    align-items: left;
    padding-bottom: 50px;
  }
  .heading_2 {
    font-size: 50px;
  }

  .desc {
    width: 99%;
    text-align: left;
    color: var(--slate);
  }
  .right {
    width: 95%;
    align-items: flex-end;
  }
  .form {
    width: 90%;
    padding: 10px;
  }
  .form input,
  .form textarea {
    padding: 10px 12px;
    margin: 10px 0;
    font-size: var(--fz-xl);
    font-weight: 300;
    word-spacing: 2px;
    letter-spacing: 2px;
  }
  .btn {
    font-size: var(--fz-md);
    padding: 15px 20px;
    width: 150px;
    margin-top: 20px;
    align-self: left;
  }
}
@media screen and (min-width: 1501px) {
  .project {
    flex-direction: row;
    height: auto;
  }
  .left {
    width: 95%;
    align-items: left;
    padding-bottom: 50px;
  }
  .heading_2 {
    font-size: 50px;
  }

  .desc {
    width: 99%;
    text-align: left;
    color: var(--slate);
  }
  .right {
    width: 95%;
    align-items: flex-end;
  }
  .form {
    width: 90%;
    padding: 10px;
  }
  .form input,
  .form textarea {
    padding: 10px 12px;
    margin: 10px 0;
    font-size: var(--fz-xxl);
    font-weight: 300;
    word-spacing: 3px;
    letter-spacing: 2px;
  }
  .btn {
    font-size: var(--fz-lg);
    padding: 15px 20px;
    width: 200px;
    margin-top: 20px;
    align-self: left;
  }
}
