.container {
  display: flex;
  justify-content: center;
  height: 50;
  top: 10px;
  position: relative;
  text-align: center;
  z-index: 2;
  box-shadow: 0 -5px 5px -5px var(--light-slate);
}
.text {
  font-size: var(--fz-md);
  color: var(--light-slate);
  font-weight: 500;
}
.text > a {
  cursor: pointer;
}
.text > a:hover {
  color: var(--green);
}
