.main {
  /* border: 1px solid hotpink; */
  width: 90%;
  padding: 15px;
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  text-align: center;
  column-gap: 1rem;
  row-gap: 1rem;
}
/* .main > div {
  border: 2px solid green;
  padding-top: 10px;
  text-align: center;
} */
.main > div {
  border: 0.5px solid var(--dark-navy);
  height: 150px;
  width: 150px;
  padding: 10px;
  margin: 0;
  padding-top: 10px;
  text-align: center;
  margin: auto;
  box-shadow: 5px -5px 5px 0px var(--dark-navy);
  border-radius: 1rem 0 1rem 0;
}

/* .main > div > img {
  transition: transform 0.5s ease;
  width: 100%;
  max-width: 200px;
} */
.main > div > img {
  transition: transform 0.5s ease;
  width: 100%;
  max-width: 120px;
  padding: 0;
  margin: 0;
}
.main > div > img:hover {
  padding: 0;
  margin: 0;
  transform: scale(1.1);
}
/* .main > div > p {
  font-size: var(--fz-xxl);
  transition: transform 1s ease;
} */
.main > div > p {
  padding: 0;
  margin: 0;
  font-size: var(--fz-lg);
  transition: transform 1s ease;
}
.main > div > p:hover {
  color: var(--green);
  transform: scale(1.3);
}
.image:hover ~ .title {
  color: var(--green);
  transform: scale(1.3);
}

/* githubStats */
.container2 {
  /* border: 1px solid red; */
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin: 3rem 0;
  gap: 30px;
}
@media screen and (max-width: 599px) {
  .main > div {
    height: 100px;
    width: 100px;
  }
  .main > div > img {
    width: 100%;
    max-width: 80px;
  }
  .main > div > p {
    font-size: var(--fz-sm);
  }
  .container2 {
    margin: 0;
    gap: 10px;
  }
}

@media screen and (min-width: 600px) and (max-width: 899px) {
  .main > div {
    height: 120px;
    width: 120px;
  }
  .main > div > img {
    width: 100%;
    max-width: 100px;
  }
  .main > div > p {
    font-size: var(--fz-md);
  }
}
@media screen and (min-width: 900px) and (max-width: 1200px) {
  .main > div {
    height: 130px;
    width: 130px;
  }
  .main > div > img {
    width: 100%;
    max-width: 110px;
  }
  .main > div > p {
    font-size: var(--fz-lg);
  }
}

