.calendar {
  /* border: 1px solid red; */
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  height: auto;
  margin: 15px 0;
}

.stats {
  /* border: 1px solid red; */
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  height: auto;
  margin: 15px 0;
}
.stats > div {
  /* border: 1px solid red; */
  width: 45%;
}

.stats > div > a > img {
  width: 100%;
}
@media screen and (max-width: 800px) {
  .stats {
    flex-direction: column;
    justify-content: center;
  }
  .stats > div {
    width: 100%;
    margin: 15px 0;
  }
}

@media screen and (min-width: 801px) and (max-width: 1000px) {
}
@media screen and (min-width: 1001px) and (max-width: 1200px) {
  .stats {
    width: 100%;
  }
  .stats > div {
    width: 47%;
  }
}
